<template>
<!-- 注意：<better-scroll> 需要绑定数据本省封装的时候也需要传入一个data的，知道数据长度才能计算长度。其次数据通过props通过父组件singer.vue把处理好的数据singerList[]传入这个组件类，然后循环渲染-->
  <scroll-box class="singer-scroll-box":data = 'data' ref = "scorrlTo">
     <ul>
       <li v-for="group in data" ref="listgroup">
        <!-- 标题 -->
          <h1  class="singer-list-title">{{group.title}}</h1>
        <!-- 歌手列表 -->
          <ul>
            <li class="singer-list-items" v-for="item in group.items">
              <img v-lazy="item.imgUrl" alt="" class="singer-img">
              <span class="singer-name">{{item.singerName}}</span>
            </li>
          </ul>
       </li>
     </ul>

     <!-- 右边的索引 -->
     <!-- @touchstart这个是better-scoll库的方法，vue是 v-on:ckick="" -->
     <div class="select-titile" @touchstart="onTouchTitle">
        <ul>
        <!--这里给每个索引绑定一个自定义属性，循环的索引 :el-index="index" -->
          <li class="title-item" v-for="(item,index) in itemTitle" :el-index="index">{{item}}</li>
        </ul>
     </div>
  </scroll-box>
</template>

<script>
import ScrollBox from '@/components/base/ScrollBase/ScrollBase'
import {myDOM} from '@/common/js/Dom.js'
export default {
  components:{
      ScrollBox
  },
  props:{
      data:{
          type:Array,
          default:[]
      }
  },
  data(){
    return {
    }
  },
  watch:{},
 beforeRouteEnter (to, from, next){next()},
 beforeRouteUpdate (to, from, next){next()},
 beforeRouteLeave (to, from, next){next()},
  computed:{
      // 只要在它的函数里引用了 data 中的某个属性，当这个属性发生变化时，函数仿佛可以嗅探到这个变化，并自动重新执行。
      itemTitle (){
          return this.data.map((group) =>{
              // 把数据中 titl取出来渲染右边的索引
              return group.title.substr(0,1)
          })
      }
  },
  methods:{
      // 点击右侧的索引字母，列表滚动到指定的区域方法
      onTouchTitle (e){
          //拿到索引上的自定义属性index
          // e.target指的是该对象里的子对象，也是触发这个事件的对象！
          let titleIndex = myDOM.setElmentAttribute(e.target, 'index')
          this.$refs.scorrlTo.scrollToElement(this.$refs.listgroup[titleIndex],0)
      }
  },
 beforeCreate () {},
  created(){},
  mounted(){},
  updated () {},
  destroyed () {},
}
</script>
<style lang="scss" scoped>
@import '~@/common/scss/const.scss';
@import '~@/common/scss/mymixin.scss';
.singer-scroll-box{
    height: 100%;
    width: 100%;
    overflow: hidden;
    background: $color-background;
    .singer-list-title{
        height: 1.875rem;//30px
        line-height: 1.875rem; //30px
        padding-left: 1.25rem;//20px
        font-size: $font-size-small;
        color: $color-text-l;
        background: $color-highlight-background;
    }
    //每个歌手item样式
    .singer-list-items{
        display: flex;
        align-items: center;
        // border: 1px solid white;
        padding: 20px 0 0 30px;
        .singer-img{
            height: 3.125rem;//50px
            width: 3.125rem;//50px
            border-radius: 50%;
            // border: 1px solid white;
        }
        .singer-name{
            margin-left: 20px;
            color: $color-text-l;
            font-size: $font-size-medium
        }
    }
    // 右边索引
    .select-titile{
        position: absolute;
        z-index: 30;
        top: 7%;
        right: 2%;
        min-height: 1px;
        width: 1.25rem;// 20px
        text-align: center;
        background: $color-background-d;
        font-family: Helvetica;
        .title-item{
            padding: 3px;
            font-size: $font-size-small;
            color: $color-text-l;
        }
    }

}

</style>


