import jsonp from '@/common/js/jsonp'
import { commonParams, opts } from '@/api/paramConfig.js'
import axios from 'axios'
/**
 * jsonp 抓取推荐页轮播图数据
 * 接口：https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg
 * 这个是jsonp的请求方式
 */
export function youMyaLikeData() {
  let url = 'https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg'
  // Object.assign是ES6新添加的接口，主要的用途是用来合并多个JavaScript的对象。
  let data = Object.assign({}, commonParams, {
    platform: 'h5',
    uin: 0,
    needNewCode: 1
  })
  return jsonp(url, data, opts)
}
/**
 * axios 抓取推荐页列表数据
 * 接口：https://c.y.qq.com/splcloud/fcgi-bin/fcg_get_diss_by_tag.fcg
 * 接口提供方使用了 referer:https://y.qq.com/portal/playlist.html
 * axios 结合 node.js 代理后端请求，不是jsonp请求
 */
export function getList() {
  let url = '/api/getList'
  let data = Object.assign({}, commonParams, {
    // 以下参数应该和QQ官方的参数保持一致就可以，通过谷歌浏览器 检查-》network,http请求的Query String Param查看
    rnd: Math.random(),
    hostUin: 0,
    format: 'json', // 请求的数据格式是json格式
    platform: 'yqq',
    needNewCode: 0,
    categoryId: 10000000,
    sortId: 5,
    sin: 0,
    ein: 29
  })
  return axios.get(url, {
    params: data // 把请求参数传入
  }).then(function(response) {
    return Promise.resolve(response.data)
  }).catch(function(error) {
    console.log(error)
  })
}



