<template>
  <div class="singer-main">
    <singer-list :data ="singerList"></singer-list>
  </div>
</template>

<script>
import {getSingers} from '@/api/getSingers.js'
import {singer} from '@/common/js/classSinger.js'
import SingerList from '@/components/base/SingerList/SingerList'
// 定义全局变量
const HOT_TITILE ='热门';
const HOT_LENGTH = 10;
export default {
 
  props:{},
  data(){
    return {
      singerList:[]
    }
  },
  components:{
    SingerList

  },
  created() {
    this._getSingers()
  },
  computed:{},
  methods:{
    // 获取歌手数据
    _getSingers (){
      getSingers ().then(res =>{
        if(res.code === 0){
          this.singerList = this._resetSingerData( res.data.list);
          console.log( this.singerList)
        }
      })

    },
    // 由需求是要求渲染的数据要进行排序  热门（前10位歌手）--> 字母[a-z] 排序，拿到的数据不符合要求所以需要写一个方法来处理我们拿到的这个数据以正确渲染
    _resetSingerData (list){
      // 首先，把数据当中的前10条数据归到一个数组中
      // 热门歌手
      let map = {
        hot: {
          title: HOT_TITILE,
          items: []
        }
      }
      list.forEach((item, index) => {
        // 把前10个歌手归入到一个热门数组里
        if (index < HOT_LENGTH){
          //实例化singer，也就是把下面的代码简单的封装成一个singer对象后，只传入一个id  和 name,也方便后面组件img标签src渲染，就不用另外写js去控制图片img  src的地址
          map.hot.items.push(new singer({
            id :item.Fsinger_mid,
            name:item.Fsinger_name
          }))
          // map.hot.items.push({
          //   singerName :item.Fsinger_name,
          //   singerId : item.Fsinger_id,
          //   singerImgId : item.Fsinger_mid,
          //   // 接口显示：歌手头像的url其实是和歌手id绑定的。
          //   imgUrl : `https://y.gtimg.cn/music/photo_new/T001R300x300M000${item.Fsinger_id}.jpg?max_age=2592000`
          // })
        }
        // 然后，把所有歌手按字母a-z也就是按接口的item.Findex为准进行聚合
        const key = item.Findex;
        if(!map[key]){
          map[key] = {
            title : key,
            items : []
          }
          // console.log("这是map[key]"+ map[item.Findex])
          // 这里的map[key]一个对象,一个map{key:{title:''   item:[]}}的对象
        }
          map[key].items.push(new singer({
            id :item.Fsinger_mid,
            name:item.Fsinger_name
        }))    
      });
      // console.log(map)
    // VUE是可以循环对象的，v-for就可以把数据循环渲染上去，但是vue循环对象是无序的，经过以上的数据聚合，还需要把数据顺序进行重新排序
        let hot =[];
        let atoz =[];
        for(let i in map){
          let mapValue = map[i]
          if(mapValue.title.match(/[A-Za-z]/)){
            atoz.push(mapValue)
          }else if (mapValue.title ===HOT_TITILE){
            hot.push(mapValue)
          }
        }
        //把atoz进行A-Z的排序
        // 数组sort的用法：
        //如果想按照其他标准进行排序，就需要提供比较函数，该函数要比较两个值，然后返回一个用于说明这两个值的相对顺序的数字。比较函数应该具有两个参数 a 和 b，其返回值如下：
        //若 a 小于 b，在排序后的数组中 a 应该出现在 b 之前，则返回一个小于 0 的值。
        //若 a 等于 b，则返回 0。
        //若 a 大于 b，则返回一个大于 0 的值
        atoz.sort((a,b) =>{
          // charCodeAt() 方法可返回指定位置的字符的 Unicode 编码。这个返回值是 0 - 65535 之间的整数。
          //strObj.charCodeAt(index) 一个字符串中的第一个字符编号为 0，第二个字符编号为 1，依此类推。
          return a.title.charCodeAt(0)-b.title.charCodeAt(0);
          //比较两个数的大小用的,然后返回结果的正负作为排序的依据.这个函数是升序排序,如果想逆序排序改成return b-a;就行了,
          //它的排序原理是每2个数比较,然后根据正负更改数组内元素的位置.

        })
        //然后把两个数组连起来 return出去
        return hot.concat(atoz)
        
    }
  },
}
</script>
<style lang="scss" scoped>
@import '~@/common/scss/const.scss';
@import '~@/common/scss/mymixin.scss';
.singer-main{
  position: fixed;
  width: 100%;
  // border: 1px solid white;
  top :88px;
  height: 100%;
}
</style>


