<template>
<!-- 轮播组件 -->
  <div class="slider-box" ref="slider">
    <!-- 播放窗口 -->
    <div class="slider-group" ref="sliderGroup">
        <!-- 定义个插槽，好在外部引用这个组件得时候在这个组件得标签里面写DOM代码。 -->
         <slot></slot>
    </div>
      <!-- 点点 -->
    <div class="dots">
        <span v-for="(item , index) in dots" :class="{active : currentDot === index}" class="dot"></span>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
// import { myDOM } from '@/common/js/Dom.js'
export default {
  components:{},
  props:{
    // 是否循环播放
    loop: {
      type: Boolean,
      default: true
    },
    // 是否自动播放
    autoPlay: {
      type: Boolean,
      default: true
    },
    // 轮播延时
    delay: {
      type: Number,
      default: 3000
    }
  },
    mounted () {
    setTimeout(() => {
      this._setSliderWith()
      this._initSlider()
      this._initDots ()
      // 如果自动轮播为true 就定义一个自动播放的方法
      if (this.autoPlay){
        this._play()
      }
    }, 20)
    // 当页面的视口发生变化的时候要重新计算轮播宽度，_setSliderWith()
    // window.addEventListener('resize' ,()=>{
    //   if (!this.slider) {
    //     return
    //   }
    //   this._setSliderWith (true);
    //   this.slider.refresh()// refresh()是 better-scroll自带的接口，用于刷新轮播，重新初始化

    // })
  },
  data(){
    return {
      dots:[ ],
      currentDot:0 // 轮播当前页所对应的点
    }
  },
  watch:{},
  computed:{},
  methods:{
    // 计算并且设置轮播的宽度
    _setSliderWith (resize) {
      // 接口传过来的每个图片的div
      this.children = this.$refs.sliderGroup.children;
      // 父元素的宽度
      let fatherWith = 0;
      // 子元素的宽度
      let childWith = this.$refs.sliderGroup.clientWidth;
      // console.log(childWith)
      // 这里动态给每个子元素动态添加一个样式
      for (let i = 0; i < this.children.length; i++){
        // 获取每个循环的div子元素
        let chidEle = this.children[i];
        console.log(chidEle)
        // myDOM.addclass(chidEle,'swiper-items');
        // 每一个chidEle的宽度实际就是父容器的 宽度
        chidEle.style.width = childWith + 'px';
        // 总的父元素的宽度累加每个子元素的宽度
        fatherWith += childWith;
        
      }
      // 实际better-scroll无缝循环的时候，为了达到无缝轮播的效果，父元素的宽度在一次轮播的时候实际上是2个子元素的宽度（前面一个后面一个），所以当循环播放是ture的时候，要累加2倍宽度
      if (this.loop){
        fatherWith += 2*childWith;
      }
       this.$refs.sliderGroup.style.width = fatherWith + 'px'
        console.log(fatherWith)
    },
    // 初始化 better-scroll
    _initSlider () {
      this.slider = new BScroll(this.$refs.slider, {
        scrollX: true, // 横向轮播
        scrollY: false,// 纵向轮播
        momentum: false, //滑动惯性
        snap: true, // 此属性就是给slider组件用的
        snapLoop: this.loop, // 是否无缝轮播
        snapThreshold: 0.3, // 手指滑动
        snapSpeed: 400 // 轮播速度
      })
      // better-scroll里面有一个在每个轮播结束的时候会派发一个轮播结束的事件，回调函数里利用这个事件来和dots来对接
      // 这个回调函数就是better-scroll中的getCurrentPage()的pageX,它就是当前第几个子元素
      this.slider.on ('scrollEnd', () =>{
        let nowIndex = this.slider.getCurrentPage().pageX;
        if(this.loop){
          // 要是无缝轮播为true的话，由于better-scroll无缝轮播会拷贝一个，所以这里真实的轮播索引应该减去一个。
          nowIndex -= 1;
        }
        this.currentDot = nowIndex;
        // 不断清除定时器实现连续轮播
        if (this.loop){
          clearTimeout (this.timer);
          this._play();
        }
        // console.log(this.currentDot)
      })
    },
    //初始化轮播小点，轮播小点的个数等于请求过来的数据长度
    _initDots () {
      this.dots = new Array (this.children.length)
    },
    // 轮播播放
    _play(){
      let nextPage = this.currentDot +1;
      if(this.loop){
        nextPage +=1;
      }
      // 自动播放
      this.timer = setTimeout (()=>{
        // 这里的 better-scroll 提供了一个播放功能的接口 goToPage
        this.slider.goToPage (nextPage,0,400)
      },this.delay)// delay是否轮播延时
    }
  },
 beforeCreate () {},
  created(){},
  updated () {},
  destroyed () {
    // 养成良好习惯，及时释放内存
    clearTimeout (this.timer)
  },
}
</script>
<style  lang="scss" scoped>
@import '~@/common/scss/const.scss';
// position的默认值是static，（也就是说对于任意一个元素，如果没有定义它的position属性，那么它的position:static）
// absolute是相对于自己最近的父元素来定位的，如果你不给#demo相对定位，那么#sub的绝对定位就是相对于body来定位的。
// 另：relative 不脱离文档流，absolute 脱离文档流。也就是说：relative 的元素尽管表面上看到它偏离了原来的位置，
// 但它实际上在文档流中还是没变。absolute的元素不仅位置改变了，同时也脱离了文档流。
// position:relative日常应用的时候一般是设置给position:absolute;的父层的,父层position:relative; 
// 子层position:absolute;的话, 就是依照父层的边界进行定位的, 不然position:absolute 会逐层向上寻找设置了position:relative的元素边界, 直到body元素..
.slider-box {
  min-height: 2px;//  min-height默认最小高度，该css会被里面的元素撑开，要是没有元素则默认是2px
    // border: 1px solid red;
    .sliderGroup {
      position: relative;
      overflow: hidden;
      white-space: nowrap;
        .swiper-items{
        float: left;
        box-sizing: border-box;
        text-align: center;
        overflow: hidden;
          a {
          display: block;
          width: 100%;
          overflow: hidden;
          text-decoration: none;
          }
        img {
          display: block;
          width: 100%;
          }
      }
    }
    .dots{
      margin: 0 auto; 
      position: absolute;
      left: 0;
      right: 0;
      bottom: .9375rem; // 15px
      text-align: center;
      .dot{
        display: inline-block;
        margin: 0 4px;
        height: 8px;
        width: 8px;
        border-radius: 50%;
        background: $color-text-l;
         &.active {
           width: 10px;
           height: 10px;
           background: $color-text-ll;
         }
      }
    }
}
  

</style>


