<template>
<!-- 这个公共组件用于封装滑动，实现复用。比如上下列表的滑动，封装这个组件需要用到better-scroll组件。 -->
<!-- 只需要在这个组件写成一个插槽，那么在slot插槽位置就可以嵌入任何DOM，使得这些DOM可以在这个组件包裹下实现滚动效果 -->
  <div ref="wapperBox" class="scroll-box">
     <slot></slot>
  </div>
</template>

<script>
// 引入 better-scroll库， 具体相关文档可以查看github库redme
import BScroll from 'better-scroll'
export default {
  components:{},
  props:{
// 类型：Number
// 默认值：0
// 可选值：1、2、3
// 作用：有时候我们需要知道滚动的位置。当 probeType 为 1 的时候，会非实时（屏幕滑动超过一定时间后）派发scroll 事件；
// 当 probeType 为 2 的时候，会在屏幕滑动的过程中实时的派发 scroll 事件；
// 当 probeType 为 3 的时候，不仅在屏幕滑动的过程中，而且在 momentum 滚动动画运行过程中实时派发 scroll 事件。
// 具体的参数可阅读better-scroll库github的文档。
    probeType: {
      type: Number,
      default: 1
    },
    // click: 点击事件是否生效
     click: {
      type: Boolean,
      default: true
    },
    // 传入的数据
    data: {
      type: Array,
      default: null
    },
  },
  data(){
    return {
    }
  },
  watch:{
    // 监听数据变化的时候，刷新better-scroll
    data () {
      setTimeout(() => {
        this.refresh()
      })
    }
  },
  computed:{},
  methods:{
      _initScroll(){
          if(!this.$refs.wapperBox){
              return
          }
          // 初始化 better-scroll 库，相关用法请参照github
          this.scroll = new BScroll (this.$refs.wapperBox, {
            probeType: this.probeType,
            click: this.click
          })
      },
    // 相关用法请参照github
    enable() {
      this.scroll && this.scroll.enable()
    },
    // 相关用法请参照github
    disable() {
      this.scroll && this.scroll.disable()
    },
    // 当数据有变化的时候刷新，以便重新计算高度
     refresh() {
      this.scroll && this.scroll.refresh()
    },
    // 歌手组件会用到，让scroll滚动到相应位置。这里 所有的方法都是调用better-scroll的封装
    scrollTo() {
      // apply:方法能劫持另外一个对象的方法，继承另外一个对象的属性.
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
    },
    scrollToElement() {
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
    }
  },
 beforeCreate () {},
  created(){},
  mounted(){
      setTimeout (() =>{
         this._initScroll()
      },20)
  },
  updated () {},
  destroyed () {},
}
</script>
<style scoped>
</style>


