import Vue from 'vue'
import Router from 'vue-router'
// 导航路由
import Search from '@/components/Search/Search'
import Singers from '@/components/Singers/Singers'
import TopSongs from '@/components/TopSongs/Tops'
import YouMayLike from '@/components/YouMayLike/YouMayLike'

Vue.use(Router)

export default new Router({
  routes: [
    // 默认推荐
    {
      path: '/',
      redirect: '/YouMayLike'
    },
    // 推荐
    {
      path: '/YouMayLike',
      component: YouMayLike
    },
    // 歌手
    {
      path: '/Singers',
      component: Singers
    },
    // 排行
    {
      path: '/TopSongs',
      component: TopSongs
    },
    // 搜索
    {
      path: '/Search',
      component: Search
    }
  ]
})



