/*
这里放一些相关DMO操作的通用方法，模块化开发
*/
export const myDOM = {
// 用于判断这个元素里面是否又classname
  hasClass (el, classname) {
    let reg = new RegExp(('^|\\s') + classname + ('\\s|$'))
    return reg.test(el.classname)
  },
  // 用于给元素添加classname
  addclass(el, classname) {
    console.log(111)
    if (this.hasClass(el, classname)) {
      return
    }
    let newClass = el.classname.split(' ')
    newClass.push(classname)
    el.classname = newClass.join(' ')
  },
  /**
   * 在事件中获取设置 自定义属性 el-${name}=val
   * 如果传入了 val 就设置自定义属性 `el-${name}` 的值为 val
   * 如果没有传入了 val 就获取自定义属性 `el-${name}` 的值
   */
  setElmentAttribute (el, name, val) {
    if (val) {
      return el.setAttribute(`el-${name}`, val)
    } else {
      return el.getAttribute(`el-${name}`)
    }
  }
}



