<template>
  <div class="m-loading">
    <img src="./loading.gif" alt="">
    <p class="loading-title">{{loadText}}</p>
  </div>
</template>

<script>
export default {
  components:{},
  props:{
      loadText :{
          type :String,
          default:'正在玩命加载中.....'
      }
  },
  data(){
    return {
    }
  },
  watch:{},
  computed:{},
  methods:{},
 beforeCreate () {},
  created(){},
  mounted(){},
  updated () {},
  destroyed () {},
}
</script>
<style lang="scss"scoped>
@import '~@/common/scss/const.scss';
.m-loading{
    width: 100%;
    text-align: center;
    img{
        height: 30px;
        width: 30px;  
    }
    .loading-title{
        font-size: $font-size-small;
        color: $color-text-l; 
    }

}
</style>


