<template>
<div class="youLike-box">
  <!-- 这里由于抽象了一个滑动组件scroll-box基于better-scroll库，在scroll-box种写了一个wath监听事件 -->
  <!-- wath监听事件在于数据比DOM结构慢一些，而封装的滑动组件里初始化better-scroll在 mounted里即在dom渲染完成了后，但是这个时候数据还没过来，
  所以在scroll-box里绑定一个likeList，等这个数据过来的时候执行刷新动作，重新初始化组件 -->
  <scroll-box class="slider-component" :data ="likeList">
    <!-- 为避免轮播插件出错，写个v-if 目的在于请求到数据后再渲染dom -->
  <div>
    <!-- 注意：better-scroll组件库的话必须DOM层级是父子关系才能生效，所以scroll-box下所有要滑动的内容必须用一个div包裹住 -->
    <div v-if="youMayLikeData.length" class="slide-wrapper">
      <m-slider>
        <!-- 这是插槽里的内容 -->
        <div v-for="item in youMayLikeData" class="swiper-items">
          <a :href="item.linkUrl">
            <!-- fastclick会阻止拦截用户快速点击，如果这时候需要点击则可以在需要点击的元素上加一个needsclick类就可以了 -->
            <img :src="item.picUrl" class="needsclick">
          </a>
         </div>
      </m-slider>
    </div>
    <!-- 歌单 -->
    <div class="youmaylike-songs" :data ="likeList">
          <h1 class="list-titile">热门歌单推荐</h1>
          <ul class="songList-ul">
            <li class="list-item" v-for="item in likeList">
              <!--左侧 图片 -->
              <div class="item-img">
                <img v-lazy="item.imgurl" alt="">
              </div>
              <!-- 右侧文字描述部分 -->
              <div class="item-texts">
                <!-- v-html 的功能在于如果数据种出现了乱码则渲染DOM的时候自动转义以正常显示 -->
                <p v-html="item.creator.name"class="song-name"></p>
                <p v-html="item.dissname"class="song-texts"></p>
              </div>
          </li>
          </ul>
    </div>
  </div>
  <!-- 加载动画组件 -->
  <div class="loading-component" v-show="!likeList.length">
    <!-- 当 likeList.length为false的时候显示该组件 -->
    <loading></loading>
  </div>
  </scroll-box>
</div>
</template>

<script>
import Loading from '@/components/base/loading/loading'
import ScrollBox from '@/components/base/ScrollBase/ScrollBase'
import MSlider from '@/components/base/Sliders/Sliders'
import { youMyaLikeData ,getList} from '@/api/youmaylike.js'
export default {
  components:{
    MSlider,
    ScrollBox,
    Loading
  },
  props:{},
  data(){
    return {
       youMayLikeData: [],
       likeList:[] 
    }
  },
  created() {
    this.youMyaLikeData()
    setTimeout(() =>{
      this._getMayLikeList()
    },1000)
    
  },
  watch:{},
  computed:{},
  methods:{
    // 从QQ接口获取轮播数据，youMyaLikeData是api/youmaylike.js封装的函数
    youMyaLikeData() {
      youMyaLikeData().then((res) =>{
        if(res.code === 0){
          this.youMayLikeData = res.data.slider
        }
      })
    },
    //获取推荐歌单的数据 非jsonp,通过后端代理请求获得
    _getMayLikeList() {
      getList().then((res) =>{
        if(res.code === 0){
          this.likeList = res.data.list;
          console.log(this.likeList)
        }
      })
    }
  },

}
</script>
<style lang="scss" scoped>
@import '~@/common/scss/const.scss';
@import '~@/common/scss/mymixin.scss';
.youLike-box{
  position: fixed;
  width: 100%;
  top: 88px;
  bottom: 0;
  .loading-component{
    // border :1px solid white;
    position: absolute;
    width: 100%;
    top: 40%;
    // 元素往左位移自身宽度50%的距离
    transform: translateY(-50%);  
  }
 .slider-component {
   height: 100%;
   overflow: hidden;
  .slide-wrapper{
      position: relative;
      width: 100%;
      overflow: hidden;
        .swiper-items{
        position: relative;
        overflow: hidden;
        float: left;
        box-sizing: border-box;
        text-align: center;
        a {
          display: block;
          width: 100%;
          overflow: hidden;
          text-decoration: none;
          }
        img {
          display: block;
          width: 100%;
        }
      }
    }
  // 歌单样式
  .youmaylike-songs{
    min-height: 1px;
    width: 100%;
    margin-top: 1.625rem; // 10px
    // border: 1px solid white;
    .list-titile{
      text-align: center;
      font-size: $font-size-medium;
      color: $color-theme;  
    }
    .songList-ul{
      margin-top: 1.25rem;// 20px;
      .list-item{
        margin-top: 15px;
        display: flex;
        // justify-content: space-around;
        box-sizing: border-box;
        align-items: center;
        padding: 0 20px 20px 20px;
        .item-img{
          width: 60px;
          flex: 0 0 60px;
          // background: red;
          margin-right: 20px;
          // border:  1px solid white;
          img{
            width: 100%;
          }
        }
        .item-texts{
          display: flex;
          justify-content: center;
          flex-direction: column;
          // border:  1px solid white;
          flex: 1;
          min-width:1px;
          height: 60px;
          .song-name{
            margin-bottom: 10px;
            color: $color-text;
            }
          .song-texts{
           color: $color-text-d;
            }

          }

        }

      }
    }
  }
}

</style>


