<template>
  <div id="app">
    <el-myheade></el-myheade>
    <!-- 导航栏 -->
    <el-mtab></el-mtab>
    <!-- 通过keep-alive标签包裹后，内容在第一次生命周期请求数据后会缓存在内存中，避免切换标签的时候又重新请求数据，slider也不会出现闪的情况 -->
    <keep-alive> 
       <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
import MyHeade from '@/components/MyHeader/MyHeader'
import Mtab from '@/components/Mtab/Mtab'
export default {
  name : 'App',
  components:{
     "el-myheade": MyHeade,
     "el-mtab": Mtab
  }
}
</script>

<style lang="scss" scoped></style>




