// 封装一个singer类，去掉组件singer imgurl重复部分，也方便后面singerList img标签传入url,url 因为在html是不好直接用变量代替src地址的
export class singer {
  constructor ({id, name}) {
    this.singerName = name
    this.singerId = id
    this.imgUrl = `https://y.gtimg.cn/music/photo_new/T001R300x300M000${id}.jpg?max_age=2592000`
  }
}

